.App {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    max-width: 1300px;
    margin: 0 auto;

    grid-template-areas:
        "header header header"
        "navigation content content "
        "footer footer footer";
}

.app-wrapper-content {
    grid-area: content;
    background-color: antiquewhite;
}





