.memberProfile {
    border: 3px solid rgb(22, 119, 255);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-radius: 10px;
    gap: 5px;
}

.memberPhoto {
    max-width: 110px;
    max-height: 110px;
    border-radius: 60%;
}